import React, { useState } from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";

import { MenuContainer, MenuTitle, MenuContent } from '../styles/Admin'

import { ReactComponent as PatientIcon } from '../assets/icons/medico.svg';
import { ReactComponent as CalendarIcon } from '../assets/icons/cita-medica.svg';
import { ReactComponent as EditIcon } from '../assets/icons/editar.svg';
import { ReactComponent as ExitIcon } from '../assets/icons/cerrar-sesion.svg';
import { ReactComponent as DecideIcon } from '../assets/icons/decide.svg';

function Menu({ setMenuOpened, userType, logout }) {

    const menuInitialWidth = 4

    const [width, setWidth] = useState(menuInitialWidth)

    const toggleMennu = () => {
        setWidth( width === menuInitialWidth ? 15 : menuInitialWidth )
        setMenuOpened( width === menuInitialWidth)
        console.log(width === menuInitialWidth)
    }

    const gotoDates = () => window.location.replace(`/admin/${userType.toLowerCase()}`)
    const gotoPatients = () => window.location.replace(`/admin/${userType.toLowerCase()}/pacientes`)
    const gotoAproveDates = () => window.location.replace(`/admin/${userType.toLowerCase()}/aprobar`)

    return (
        <MenuContainer width={`${width}vw`}>
            <MenuTitle bgColor={width === menuInitialWidth ? 'rgb(240, 240, 240)' : 'white'}>
                <div>
                    {width === menuInitialWidth ? <FontAwesomeIcon icon={faBars} color="rgb(0,47,91)" onClick={toggleMennu}/> :
                    <FontAwesomeIcon icon={faTimes} color="rgb(0,47,91)" onClick={toggleMennu} />}
                </div>
                <h1>Menú</h1>
            </MenuTitle>
            <MenuContent opacity={width === menuInitialWidth ? 0 : 1}>
                <div onClick={gotoPatients}>
                    <PatientIcon width="3vw" height="3vw"/>
                    <h2>Pacientes</h2>
                </div>
                <div onClick={gotoDates}>
                    <CalendarIcon width="3vw" height="3vw"/>
                    <h2>Citas</h2>
                </div>
                <div onClick={gotoAproveDates}>
                    <DecideIcon width="3vw" height="3vw"/>
                    <h2>Aprobar</h2>
                </div>
                <div>
                    <EditIcon width="2.5vw" height="2.5vw"/>
                    <h2>Editar perfil</h2>
                </div>
                <div onClick={logout}> 
                    <ExitIcon width="2.5vw" height="2.5vw"/>
                    <h2>Cerrar sesión</h2>
                </div>
            </MenuContent>
        </MenuContainer>
    )
}

export default Menu
